export default {
  name: 'Tada',
  props: {
    duration: {
      type: String,
      default: '1s',
    },
    delay: {
      type: String,
      default: '0s',
    },
  },
  computed: {
    animationStyle() {
      return {
        animationDuration: this.duration,
        animationDelay: this.delay,
        animationFillMode: 'both',
      };
    },
  },
  mounted() {
    const el = this.$refs.animatedElement;
    // 啟動動畫
    requestAnimationFrame(() => {
      el.classList.add('animate');
      el.classList.remove('enter-anim');
    });
  },
};
